"use client";
import React, { useEffect, useState } from "react";
import { Container, Field } from "./InfoCard.styles";
import { H2 } from "@/components/Typography/Heading.styles";
import { Body3 } from "@/components/Typography/Body.styles";
import Image from "next/image";

interface InfoProps {
  fullname: string;
  biography: string;
  contact: ContactProps[];
  interest: InterestProps[];
  avatar: AvatarProps;
}

interface ContactProps {
  id: number;
  platform: string;
  url: string;
}

interface InterestProps {
  id: number;
  interest: string;
}

interface AvatarProps {
  id: string;
  url: string;
  name: string;
  formats?: {
    thumbnail?: { url: string };
    small?: { url: string };
    medium?: { url: string };
    large?: { url: string };
  };
}

const InfoCard = () => {
  const [info, setInfo] = useState<InfoProps | null>(null);
  const [loading, setLoading] = useState(false);
  const handleGetInfo = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "http://localhost:1337/api/authors?populate=*",
        { method: "GET" }
      );

      const data = await response.json();

      if (!response.ok) {
        setLoading(false);
      }

      setInfo(data.data[0]);
      console.log(data.data);
    } catch (error) {
      console.log("Error: " + error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGetInfo();
  }, []);

  return (
    <Container>
      <Image
        src={`http://localhost:1337${
          info?.avatar?.url ?? info?.avatar?.formats?.thumbnail?.url
        }`}
        alt="avatar"
        width={220}
        height={220}
        className="rounded-full"
      />
      <H2>my {info?.fullname} diary</H2>
      <Field>
        {info?.interest.map((item, index) => (
          <Body3 key={index}>{item.interest}</Body3>
        ))}
      </Field>
      <Body3 $align="center">{info?.biography}</Body3>
      <Field>
        {info?.contact.map((item, index) => (
          <a href={item.url} key={index}>
            Items
          </a>
        ))}
      </Field>
    </Container>
  );
};

export default InfoCard;
